body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.animation_wrap {
  font-family: "Microsoft Yahei", "\5FAE\8F6F\96C5\9ED1";
}
figure {
  position: relative;
  float: left;
  width: 33.33%;
  height: 350px;
  overflow: hidden;
  background-color: #2f0000;
}
figure img {
  width: 100%;
  height: 100%;
  opacity: 0.8;
  -webkit-transition: all 0.35s;
  transition: all 0.35s;
}
figure figcaption {
  position: absolute;
  top: 0;
  color: #ffffff;
}
figure figcaption p,
figure figcaption h2,
figure figcaption div,
figure figcaption span {
  -webkit-transition: all 0.35s;
  transition: all 0.35s;
}
.pic1-figcaption {
  padding: 20px;
}
.pic1-figcaption p {
  background-color: #ffffff;
  color: #666666;
  margin: 5px;
  text-align: center;
  -webkit-transform: translate(-120px, 0px);
          transform: translate(-120px, 0px);
}
.pic1-figcaption p:nth-of-type(1) {
  -webkit-transition-delay: 0.05s;
          transition-delay: 0.05s;
}
.pic1-figcaption p:nth-of-type(2) {
  -webkit-transition-delay: 0.1s;
          transition-delay: 0.1s;
}
.pic1-figcaption p:nth-of-type(3) {
  -webkit-transition-delay: 0.15s;
          transition-delay: 0.15s;
}
.pic1:hover .pic1-img {
  -webkit-transform: translate(-30px, 0px);
          transform: translate(-30px, 0px);
  opacity: 0.5;
}
.pic1:hover .pic1-figcaption p {
  -webkit-transform: translate(0px, 0px);
          transform: translate(0px, 0px);
}
.pic2 {
  background-color: #030;
}
.pic2-figcaption {
  width: 100%;
  height: 100%;
}
.pic2-figcaption div {
  border: 1px solid #ffffff;
  width: 80%;
  height: 80%;
  position: absolute;
  top: 10%;
  left: 10%;
  -webkit-transform: translate(0px, -350px) rotate(0deg);
          transform: translate(0px, -350px) rotate(0deg);
}
.pic2-figcaption h2 {
  margin: 15% 0 0 15%;
}
.pic2-figcaption p {
  margin-left: 15%;
  -webkit-transform: translate(0, 50px);
          transform: translate(0, 50px);
  opacity: 0;
}
.pic2:hover figcaption p {
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
  opacity: 1;
}
.pic2:hover figcaption div {
  -webkit-transform: translate(0, 0) rotate(360deg);
          transform: translate(0, 0) rotate(360deg);
}
.pic2:hover img {
  -webkit-transform: translate(-30px, 0);
          transform: translate(-30px, 0);
  opacity: 0.5;
}
.pic3 {
  background-color: #000083;
}
.pic3-figcaption {
  top: 30%;
  left: 15%;
}
.pic3-figcaption h2 {
  -webkit-transform: skew(90deg);
          transform: skew(90deg);
}
.pic3-figcaption p {
  -webkit-transform: skew(90deg);
          transform: skew(90deg);
  -webkit-transition-delay: 0.5s;
          transition-delay: 0.5s;
}
.pic3:hover img {
  opacity: 0.5;
}
.pic3:hover figcaption h2 {
  -webkit-transform: skew(0deg);
          transform: skew(0deg);
}
.pic3:hover figcaption p {
  -webkit-transform: skew(0deg);
          transform: skew(0deg);
}
.pic4 {
  background-color: #000083;
}
.pic4-figcaption {
  width: 100%;
  height: 100%;
}
.pic4-figcaption h2 {
  margin: 15% 0 0 15%;
  -webkit-transform: scale(1.2, 1.2);
          transform: scale(1.2, 1.2);
  opacity: 0;
}
.pic4-figcaption p {
  margin-left: 15%;
  -webkit-transform: scale(1.2, 1.2);
          transform: scale(1.2, 1.2);
  opacity: 0;
}
.pic4-figcaption div {
  border: 1px solid #ffffff;
  width: 80%;
  height: 80%;
  position: absolute;
  top: 10%;
  left: 10%;
  -webkit-transform: scale(1.2, 1.2);
          transform: scale(1.2, 1.2);
  opacity: 0;
}
.pic4:hover img {
  -webkit-transform: scale(1.2, 1.2);
          transform: scale(1.2, 1.2);
  opacity: 0.5;
}
.pic4:hover figcaption h2 {
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
  opacity: 1;
}
.pic4:hover figcaption p {
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
  opacity: 1;
}
.pic4:hover figcaption div {
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
  opacity: 1;
}
@media screen and (max-width: 600px) {
  figure {
    width: 100%;
  }
}
@media screen and (min-width: 601px) and (max-width: 1000px) {
  figure {
    width: 50%;
  }
}
@media screen and (min-width: 1001px) {
  figure {
    width: 33.33%;
  }
}

.App {
  text-align: center;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-link {
  color: #61dafb;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

